import React from 'react';
import { Document  } from '@react-pdf/renderer';
import PrintHbl from './printHbl';

const UI = (props) => {
  const { details } = props;

  if (details.length === 0) return <Document></Document>

  return (<Document>
    {details.map(i =>
      <PrintHbl cargo={i.cargo} packages={i.packages} />
    )}
  </Document>);
}

export default UI;
