import React from 'react';
import { Page, Image, Text, StyleSheet  } from '@react-pdf/renderer';
import reduce from 'lodash/reduce';
import numToWord from 'business/utils/numToWord';
import { volume, volumeTotal, weightTotal } from 'business/utils/number';
import JsBarcode from 'jsbarcode';
import { clientF } from 'portal/services/client.service';

const heading = {
  top: 70,
  left: 110,
  line: 5,  
  size: 10  
};

const shipper = {
  top: 182,
  left: 72,
  line: 6,  
  size: 10  
};

const consignee = {
  top: 118,
  left: 353,
  line: 6,  
  size: 10  
};

const pack = {
  top: 375,
  left0: 25,
  left1: 52,
  left2: 200,
  left3: 305,
  left4: 355,
  left5: 405,
  left6: 455,

  adjustTop: 15, 
  size: 10
};

const text = (top, left, fontSize, width) => ({ top, left, fontSize, width: width || 220, border: 0, margin: 0, padding: 0 })
const textAbs = (top, left, fontSize, width) => ({ top, left, fontSize, width: width, border: 0, position: 'absolute' })

const packRow = (id) => ({
  ['pack' + id + 'no']:
    textAbs(pack.top + pack.adjustTop * id, pack.left0, pack.size, 20),
  ['pack' + id + 'type']:
    textAbs(pack.top + pack.adjustTop * id, pack.left1, pack.size, 130),
  ['pack' + id + 'size']:
    textAbs(pack.top + pack.adjustTop * id, pack.left2, pack.size - 1, 80),
  ['pack' + id + 'quantity']:
    textAbs(pack.top + pack.adjustTop * id, pack.left3, pack.size, 30),
  ['pack' + id + 'weight']:
    textAbs(pack.top + pack.adjustTop * id, pack.left4, pack.size, 30),
  ['pack' + id + 'volume']:
    textAbs(pack.top + pack.adjustTop * id, pack.left5, pack.size, 30),
  ['pack' + id + 'remarks']:
    textAbs(pack.top + pack.adjustTop * id, pack.left6, pack.size, 120),
})

const styles = StyleSheet.create({
  pageBackground: {
    position: 'absolute', display: 'block',
    height: 590 * 1449 / 1042, // dimesnsion of the image
    width: 590,
  },
  barcode: {
    position: 'absolute',
    top: 152,
    left: 5,
    height: 55, 
    width: 250,
  },

  date:
    text(heading.top + heading.line * 0, heading.left + 0, heading.size),
  time:
    text(heading.top + heading.line * 1, heading.left + 0, heading.size),
  cargoType:
    text(heading.top + heading.line * 2, heading.left + 0, heading.size),
  destination:
    text(heading.top + heading.line * 3, heading.left + 0, heading.size),
  hbl:
    text(heading.top + heading.line * 4, heading.left + 0, heading.size),
  
  name:
    text(shipper.top + shipper.line * 0, shipper.left + 0, 8),
  name2:
    text(shipper.top + shipper.line * 1, shipper.left + 0, 8),
  address:
    text(shipper.top + shipper.line * 2, shipper.left + 0, 8),
  address2:
    text(shipper.top + shipper.line * 3, shipper.left + 0, 8),
  qid:
    text(shipper.top + shipper.line * 4 + 5, shipper.left + 0, 9),
  contact:
    text(shipper.top + shipper.line * 5 + 6, shipper.left + 0, 9),
  ppNo:
    text(shipper.top + shipper.line * 6 + 7, shipper.left + 0, 9),
  
  slName:
    text(consignee.top + consignee.line * 0, consignee.left + 0, 8),
  slName2:
    text(consignee.top + consignee.line * 1, consignee.left + 0, 8),
  slAddress:
    text(consignee.top + consignee.line * 2, consignee.left + 0, 8),
  slAddress2:
    text(consignee.top + consignee.line * 3, consignee.left + 0, 8),
  slQid:
    text(consignee.top + consignee.line * 4 + 2, consignee.left + 0, 9),
  slContact:
    text(consignee.top + consignee.line * 5 + 3, consignee.left + 0, 9),
  slPpNo:
    text(consignee.top + consignee.line * 6 + 4, consignee.left + 0, 9),

  ...packRow(0),
  ...packRow(1),
  ...packRow(2),
  ...packRow(3),
  ...packRow(4),
  ...packRow(5),
  ...packRow(6),
  ...packRow(7),
  ...packRow(8),
  ...packRow(9),
  ...packRow(10),
  ...packRow(11),
  ...packRow(12),
  ...packRow(13),
  ...packRow(14),
  ...packRow(15),

  totalQuantity:
    textAbs(620, 105, 10),
  totalVolume:
    textAbs(635, 105, 10),
  totalWeight:
    textAbs(650, 105, 10),
  totalAmount:
    textAbs(620, 360, 10),
  totalPaid:
    textAbs(635, 360, 10),
  amountInWords:
    textAbs(675, 80, 9),
  note:
    textAbs(800, 20, 10),
});

const UI = (props) => {
  const { cargo, packages } = props;
  const packageList = [
    ...packages.slice(0, 16),
  ]
  
  let canvas;
  canvas = document.createElement('canvas');
  JsBarcode(canvas, cargo.hbl, {
    displayValue: false,
  });
  const barcode = canvas.toDataURL();
  const d = clientF(cargo.branch);

  return (
    <Page size="A4" orientation="portrait">
        <Image src={"/app/images/" + d.parent + "/" + cargo.branch + "/hbl.jpg"} style={styles.pageBackground} />
        <Image src={barcode} style={styles.barcode} /> 
        <Text style={styles.date}>{cargo.pickedUpDate || cargo.pickupDate}</Text>
        <Text style={styles.time}>{cargo.pickedUpTime || cargo.pickupTime}</Text>
        <Text style={styles.cargoType}>{cargo.cargoMode}/{cargo.deliveryType}</Text>
        <Text style={styles.destination}>{cargo.warehouseLocation || '-'}</Text>
        <Text style={styles.hbl}>{cargo.hbl}</Text>
        
        <Text style={styles.name}>{cargo.name?.substring(0, 47) || '-'}</Text>
        <Text style={styles.name2}>{cargo.name?.substring(47) || '  '}</Text>
        <Text style={styles.address}>{cargo.address?.substring(0, 47) || '-'}</Text>
        <Text style={styles.address2}>{cargo.address?.substring(47) || '  '}</Text>
        <Text style={styles.qid}>{cargo.qid || '-'}</Text>
        <Text style={styles.contact}>{cargo.contact || '-'}</Text>
        <Text style={styles.ppNo}>{cargo.ppNo || '-'}</Text>
        
        <Text style={styles.slName}>{cargo.slName?.substring(0, 47) || '-'}</Text>
        <Text style={styles.slName2}>{cargo.slName?.substring(47) || '  '}</Text>
        <Text style={styles.slAddress}>{cargo.slAddress?.substring(0, 47) || '-'}</Text>
        <Text style={styles.slAddress2}>{cargo.slAddress?.substring(47) || '  '}</Text>
        <Text style={styles.slQid}>{'  '}</Text>
        <Text style={styles.slContact}>{cargo.slContact || '-'}</Text>
        <Text style={styles.slPpNo}>{cargo.slPpNo || '-'}</Text>
        
        <Text style={styles.totalQuantity}>{reduce(packages, (t, n) => t + n.quantity, 0) || '-'}</Text>
        <Text style={styles.totalVolume}>{volumeTotal(packages) || '-'}</Text>
        <Text style={styles.totalWeight}>{weightTotal(packages) || '-'}</Text>
        <Text style={styles.totalAmount}>{cargo.paid + ' ' + cargo.currency || '-'}</Text>
        <Text style={styles.totalPaid}>{cargo.paid + ' ' + cargo.currency || '-'}</Text>
        
        <Text style={styles.amountInWords}>{(numToWord(cargo.paid) + ' only' || '-').toUpperCase()}</Text>
        <Text style={styles.note}>{cargo.note || '-'}</Text>

        {packageList.map((i, j) => <Text style={styles['pack' + j + 'no']}>{j + 1}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'type']}>{i.type}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'size']}>{'' + i.length + ' X ' + i.breadth + ' X ' + i.height}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'quantity']}>{i.quantity}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'weight']}>{i.weight}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'volume']}>{volume(i)}</Text>)}
        {packageList.map((i, j) => <Text style={styles['pack' + j + 'remarks']}>{i.remarks}</Text>)}

    </Page>
  );
}

export default UI;
